<template>
	<div>
		<div class="nft-card-fullview" :class="[fullCardVisible? 'visible':'']">
			<a href="#nftCardTop" class="nft-card-fullview-backBtn" @click="closeCard()"></a>
			<div class="nft-card-fullview-content">
				<div class="nftCardTop" id="nftCardTop"></div>
				<img :src="selectedCard.image" class="preload-img img-fluid entered loaded nft-card-image" :alt="selectedCard.player_name" v-if="isUserCard">
				<div v-else>
					<img :src="selectedCard.lineup_photo_url" class="preload-img img-fluid entered loaded nft-card-image" :alt="selectedCard.player_name">
					<div class="btn buy-btn" @click="buyNft(selectedCard.url_to_buy)">
						<span class="ton-logo"></span>
						{{ selectedCard.price_ton }}
					</div>
				</div>
				<h1 class="player-name">
					{{ selectedCard.player_name }}
				</h1>
				<div class="club-name">
					{{ selectedCard.club_name }}
				</div>

				<div v-if="isUserCard">
					<h2 class="subtitle">NFT address</h2>
					<div class="card-address">
						{{ selectedCard.nft_address }}
					</div>
				</div>

				<div v-else>
					<h2 class="subtitle">Minting data</h2>
					<div class="data-table">
						<div class="data-table-row">
							<div class="data-table-col">Price tier</div>
							<div class="data-table-col">{{ selectedCard.price_tier }}</div>
						</div>
						<div class="data-table-row">
							<div class="data-table-col">NFT left in tier</div>
							<div class="data-table-col">{{ selectedCard.nfts_left }}</div>
						</div>
						<div class="data-table-row">
							<div class="data-table-col">Released total</div>
							<div class="data-table-col">{{ selectedCard.released }}</div>
						</div>
					</div>
				</div>

				<h2 class="subtitle">Player statistics</h2>

				<div class="data-table">
					<div class="data-table-row">
						<div class="data-table-col">Points last 15</div>
						<div class="data-table-col">{{ playerInfo?.points_last_15 }} T</div>
					</div>
					<div class="data-table-row">
						<div class="data-table-col">Points last 5</div>
						<div class="data-table-col">{{ playerInfo?.points_last_5 }}  T</div>
					</div>
					<div class="data-table-row">
						<div class="data-table-col">Points last match</div>
						<div class="data-table-col">{{ playerInfo?.points_last }}  T</div>
					</div>
				</div>

				<div class="btn secondary-btn mb-4">DataHub detailed statistics</div>
			</div>
		</div>
		<QRcodeModal :link="selectedCard.url_to_buy" :show="showQRcode" v-on:closeQRcodeModal="showQRcode=false" />
	</div>
</template>
<script>
import QRcodeModal from '@/components/QRcodeModal.vue';
import { isMobile } from '@/helpers';
export default {
	name: 'NftCard',
	props: ['selectedCard', 'fullCardVisible', 'isUserCard'],
	components: { QRcodeModal },
	data()
	{
		return {
			showQRcode: false
		};
	},
	methods:
	{
		closeCard()
		{
			this.$emit('closeCard');
		},
		buyNft(link)
		{
			isMobile() ? window.location.href = link : this.showQRcode = true;
		}
	},
	computed: {
		playerInfo ()
		{
			return this.$store.getters.playerInfo;
		},
	}
};
</script>

<style lang="scss">
@import './style.scss';
</style>
