<template>
	<div>
		<!-- <pre>
			{{ wallet }}
		</pre> -->
		<div class="header">
			<LeaguesSelect />
			<div v-if="wallet" @click="activeWalletOptionMenu = !activeWalletOptionMenu" class="btn wallet-btn">
				{{ slicedUserFriendlyAddress }}
			</div>
			<div v-else @click="connectWallet" class="btn wallet-btn">
				add wallets
			</div>
			<div class="walletOptionMenu" :class="{ active: activeWalletOptionMenu }">
				<div class="closeZone" @click="activeWalletOptionMenu = false"></div>
				<div @click="disconnectWallet" class="btn logout-btn">Logout</div>
			</div>
			<!-- <button @click="send" data-toast="snackbar-cart" class="btn btn-xxs bg-blue-dark rounded-xs font-500 font-12 hide-long-text">send</button> -->
		</div>
		<QRcodeModal :link="universalLink" :show="showQRcode" v-on:closeQRcodeModal="showQRcode=false" />
	</div>
</template>
<script>
import QRcodeModal from '@/components/QRcodeModal.vue';
import LeaguesSelect from './LeaguesSelect.vue';
import { mapGetters } from 'vuex';
import { connector } from '@/api/TonConnect';
import { CHAIN, toUserFriendlyAddress } from '@tonconnect/sdk';
import { isMobile } from '@/helpers';

export default {
	name: 'BottomNavigation',
	components: { LeaguesSelect, QRcodeModal },
	data()
	{
		return {
			device: '',
			neededJson: '',
			tonLink: null,
			wallet: null,
			walletConnectionSource: {
				bridgeUrl: 'https://bridge.tonapi.io/bridge',
				universalLink: 'https://app.tonkeeper.com/ton-connect',
			},
			universalLink: '',
			activeWalletOptionMenu: false,
			showQRcode: false,
		};
	},
	mounted()
	{
		this.onStatusChange();
	},
	methods:
	{
		async connectWallet ()
		{
			await this.$store.dispatch('GET_TON_LINK');
			this.tonLink = this.$store.state.ton.tonLink;
			await this.getLink();
			isMobile() ? window.location.href = this.universalLink : this.showQRcode = true;
		},
		async getLink()
		{
			this.universalLink = connector.connect(this.walletConnectionSource, { tonProof: this.tonLink });
		},
		disconnectWallet()
		{
			this.activeWalletOptionMenu = false;
			connector.disconnect();
		},
		async onStatusChange()
		{
			await connector.onStatusChange(
				walletInfo =>
				{
					this.wallet = walletInfo;
					this.showQRcode = false;
					if (walletInfo)
					{
						this.sendToBack();
					}
					console.log(walletInfo);
				}
			);
		},
		async sendToBack()
		{
			const neededJson =
			{
				address: this.wallet.account.address,
				network: this.wallet.account.chain,
				proof:
				{
					timestamp: this.wallet.connectItems?.tonProof.proof.timestamp,
					domain: this.wallet.connectItems?.tonProof.proof.domain,
					signature: this.wallet.connectItems?.tonProof.proof.signature,
					payload: this.wallet.connectItems?.tonProof.proof.payload,
					state_init: this.wallet.account?.walletStateInit
				}
			};
			this.neededJson = neededJson;
			await this.$store.dispatch('SEND_CONNECT_INFO', neededJson);
		},
		getCards()
		{
			this.$store.dispatch('SET_ACTIVE_CLUB_ID', 0);
			this.$store.dispatch('GET_CLUB', this.activeLigueId);
			this.$store.dispatch('GET_NFT', { club: 0, league: this.activeLigueId, pos: this.activePosId });
			this.$store.dispatch('GET_NFT_NARKET_LIST', { club: this.activeClubId, league: this.activeLigueId, pos: this.activePosId });
		}
	},
	computed:
	{
		ligues ()
		{
			return this.$store.getters.ligues;
		},
		activeClubId ()
		{
			return this.$store.getters.activeClubId;
		},
		activePosId ()
		{
			return this.$store.getters.activePosId;
		},
		activeLigueId: {
			get ()
			{
				return this.$store.getters.activeLigueId;
			},
			set (value)
			{
				this.$store.dispatch('SET_ACTIVE_LIGUE_ID', value);
			}
		},
		slicedUserFriendlyAddress()
		{
			if (this.wallet)
			{
				const userFriendlyAddress = this.wallet ? toUserFriendlyAddress(this.wallet.account.address, this.wallet.account.chain === CHAIN.TESTNET) : '';
				return userFriendlyAddress.slice(0, 2) + '…' + userFriendlyAddress.slice(-5);
			}
			else
			{
				return null;
			}
		},
		...mapGetters([
			'getTonLink'
		])
	}
};
</script>
<style lang="scss">
	@import './style.scss';
</style>
