<template>
	<div id="app">
		<header-vue />
		<router-view />
		<bottom-navigation />
	</div>
</template>

<script>
import HeaderVue from '@/components/Header/Header.vue';
import BottomNavigation from '@/components/BottomNavigation/BottomNavigation.vue';

import { configureRequest } from '@/api/Configure/index.js';

export default {
	name: 'App',
	components:
	{
		BottomNavigation,
		HeaderVue,
	},
	data()
	{
		return {
			data: '',
		};
	},
	mounted()
	{
		this.$store.dispatch('GET_LIGUES');
		this.data = configureRequest.initData;
	},
	computed: {
		ligues ()
		{
			return this.$store.getters.ligues;
		},
		ligue ()
		{
			return this.$store.getters.ligue;
		},
	},
	watch: {
		ligues(ligues)
		{
			this.$store.commit('getLigue', ligues[0]);
		},
		ligue(ligue)
		{
			this.$store.dispatch('GET_CLUB', ligue.ID);
		}
	},
};
</script>

<style lang="scss">
	@import 'assets/styles/style';
</style>
